*{
    padding: 0;
    margin: 0;
    box-sizing: border-box;
    font-family: 'Roboto', sans-serif
}

:root{
    --greenColor: #365907;
    --whiteColor: #F7F7F7;
    --purpleColor: #8A199E;
    --orangeColor: #FF7D35;
    --textColor: #293133;
}


header {
    display: flex;
    align-items: center;
    justify-content: space-between;
    height: 80px;
    padding: 0 2rem;
    background-color: var(--whiteColor);
    color: var(--greenColor);
}

header img {
    width: 6%;
    object-fit: cover;
}

header nav {
    position: absolute;
    right: 2rem;
}



nav a {
    margin: 0 2rem;
    color: var(--greenColor);
    text-decoration: none;
}

nav a:hover{
    text-decoration: underline;
    cursor: pointer;
}

header .nav-btn {
    padding: 5px;
    cursor: pointer;
    background: transparent;
    border: none;
    outline: none;
    color: var(--greenColor);
    visibility: hidden;
    opacity: 0;
    font-size: 1.8rem;
}

@media only screen and (max-width:1024px) {
    header .nav-btn {
        visibility: visible;
        opacity: 1;
    }

    header nav {
        position: fixed;
        top: 0;
        left: 0;
        height: 100%;
        width: 100%;
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
        gap: 1.5rem;
        background-color: var(--whiteColor);
        transition: 1s;
        transform: translateY(-100vh);
    }

    header .responsive_nav{
        transform: none;
    }

    nav .nav-close-btn {
        position: absolute;
        top: 2rem;
        right: 2rem;
    }

    nav a {
        font-size: 1.5rem;
    }
}


.color-bar {
    height: 20px;
    background-image: linear-gradient( to right, var(--purpleColor), var(--orangeColor));
}

main {
    background-image: url(../images/main.jpg);
    background-repeat: no-repeat;
    background-size: cover;
    height: 50vh;
    width: 100vw;
}

main h1 {
    color: white;
    position: absolute;
    top: 10rem;
    left: 2rem;
    font-size: 50px;
}

main p {
    color: white;
    position: absolute;
    top: 14rem;
    left: 10rem;
}

.About {
